export default new Vuex.Store({
  state: {
    forSale: [
      { invId: 1, name: '리틀프랜즈 아스크림1', image: 'https://img1.daumcdn.net/thumb/R200x200/?fname=https%3A%2F%2Ft1.daumcdn.net%2Ffriends%2Fprod%2Fproduct%2FFRPBAPIRK0001_AW_00.jpg', price: 8000 },
      { invId: 2, name: '리틀프랜즈 아스크림2', image: 'https://img1.daumcdn.net/thumb/R200x200/?fname=https%3A%2F%2Ft1.daumcdn.net%2Ffriends%2Fprod%2Fproduct%2FFRPBRYIRK0001_AW_00.jpg', price: 8000 },
      { invId: 3, name: '리틀프랜즈 아스크림3', image: 'https://img1.daumcdn.net/thumb/R200x200/?fname=https%3A%2F%2Ft1.daumcdn.net%2Ffriends%2Fprod%2Fproduct%2FFRPBJGIRK0001_AW_00.jpg', price: 8000 },
      { invId: 4, name: '리틀프랜즈 아스크림4', image: 'https://img1.daumcdn.net/thumb/R200x200/?fname=https%3A%2F%2Ft1.daumcdn.net%2Ffriends%2Fprod%2Fproduct%2FFRPBMZIRK0001_AW_00.jpg', price: 8000 },
      { invId: 5, name: '플라잉전신키링 라이언', image: 'https://img1.daumcdn.net/thumb/R200x200/?fname=https%3A%2F%2Ft1.daumcdn.net%2Ffriends%2Fprod%2Fproduct%2FFRPBRYFBK0001_AW_00.jpg', price: 8000 },
      { invId: 6, name: '플라잉전신키링 어퍼치', image: 'https://img1.daumcdn.net/thumb/R200x200/?fname=https%3A%2F%2Ft1.daumcdn.net%2Ffriends%2Fprod%2Fproduct%2FFRPBAPFBK0002_AW_00.jpg', price: 8000 },
      { invId: 7, name: '플라잉전신키링 무지', image: 'https://img1.daumcdn.net/thumb/R200x200/?fname=https%3A%2F%2Ft1.daumcdn.net%2Ffriends%2Fprod%2Fproduct%2FFRPBMZFBK0003_AW_00.jpg', price: 8000 },
      { invId: 8, name: '플라잉전신키링 튜브', image: 'https://img1.daumcdn.net/thumb/R200x200/?fname=https%3A%2F%2Ft1.daumcdn.net%2Ffriends%2Fprod%2Fproduct%2FFRPBTBFBK0004_AW_00.jpg', price: 8000 },
      { invId: 9, name: '플라잉전신키링 네오', image: 'https://img1.daumcdn.net/thumb/R200x200/?fname=https%3A%2F%2Ft1.daumcdn.net%2Ffriends%2Fprod%2Fproduct%2FFRPBNOFBK0005_AW_00.jpg', price: 8000 },
      { invId: 10, name: '플라잉전신키링 프로도', image: 'https://img1.daumcdn.net/thumb/R200x200/?fname=https%3A%2F%2Ft1.daumcdn.net%2Ffriends%2Fprod%2Fproduct%2FFRPBFDFBK0006_AW_00.jpg', price: 8000 },
      { invId: 11, name: '카드키체인인형 네오', image: 'https://img1.daumcdn.net/thumb/R200x200/?fname=https%3A%2F%2Ft1.daumcdn.net%2Ffriends%2Fprod%2Fproduct%2FFRBPNOCKR0001_AW_00.jpg', price: 12000 },
      { invId: 12, name: '카드키체인인형 튜브', image: 'https://img1.daumcdn.net/thumb/R200x200/?fname=https%3A%2F%2Ft1.daumcdn.net%2Ffriends%2Fprod%2Fproduct%2FFRBPTBCKR0001_AW_00.jpg', price: 12000 }
    ],
    inCart: [],
    authorization: {
      email: '',
      token: '',
      login: false
    }
  },
  getters: {
    forSale: state => state.forSale,
    inCart: state => state.inCart,
    authorization: state => state.authorization,
  },
  mutations: {
    ADD_TO_CART(state, invId) { state.inCart.push(invId); },
    REMOVE_FROM_CART(state, index) { state.inCart.splice(index, 1); },
    SET_AUTHORIZATION(state, authInfo) {
      console.log('인증 정보 저장', authInfo)
      state.authorization.email = authInfo.email
      state.authorization.token = authInfo.token
      state.authorization.login = true
    },
  },
  actions: {
    addToCart(context, invId) { context.commit('ADD_TO_CART', invId); },
    removeFromCart(context, index) { context.commit('REMOVE_FROM_CART', index); },
    setAuthorization(context, authInfo) { context.commit('SET_AUTHORIZATION', authInfo); },
  },
});



